
const Program = ({program}) => {
    return (
        <a className='program' href={program.url} target="_blank">
            <div className="program-image">
                <img src={'https://www.khanacademy.org' + program.thumb}/>
            </div>
            <div className="program-details">
                <h3>{program.title}</h3>
            </div>
            <div className="program-footer">
                <span>{program.sumVotesIncremented + " Votes Â· " + program.spinoffCount + " Spin-offs"}</span>
            </div>
        </a>
    )
}

export default Program
