import React from 'react'
import jQuery from 'jquery';

import { useState, useEffect } from 'react'
import { BrowserRouter as Router, Route, HashRouter} from 'react-router-dom'

import logo from './logo.svg';
import './App.css';

// Components
import Header from './components/Header';
import Nav from './components/Nav';
import Home from './components/Home';
import Programs from './components/Programs';

const App = () => {

  const [programs, setPrograms] = useState(() => {
    return []
  })

  const fetchPrograms = () => {  //jQuery get that works with KA API and avoids CORS policy
    return jQuery.getJSON('https://www.khanacademy.org/api/internal/user/scratchpads?username=grantjenkins&limit=50&callback=?', () => {
    })
    .done((data) => {
      return data
    })
  };

  useEffect(() => {
    // const getPrograms = async () => {
    //   const programs = await fetchPrograms()
    //   setPrograms(programs)
    // }

    jQuery.when(fetchPrograms()).then((data) => {
      setPrograms(data.scratchpads)
    })
  }, [])

  return (
    <React.StrictMode>
      <HashRouter>
        <div className="App">
          <Header title="Gray Wolf <code>Dev</code>" />
          <Nav />
          <main>
            <Route path='/' exact render={(props) => (
              <Home />
            )}/>
            <Route path='/programs'>
              <Programs programs={programs} />
            </Route>
          </main>
        </div>  
      </HashRouter>
    </React.StrictMode>
  );
}

export default App;
